import React, { useEffect } from "react";
import MobileNav from "../../components/MobileNav";
import "./index.css";
import trackUserEvent from "../../helpers/trackUserEvent";

const AboutUs = () => {
  useEffect(() => {
    trackUserEvent("About Us");
  },[])
  return (
    <>
      <div className="container" id="aboutUsContainer">
        <p className="pt-5">
          Behind Embark Fragrances is the experience and passion of Divine
          Cosmetics. We’re no strangers to the beauty business, having built a
          wide variety of beauty brands over decades. Along the way, we’ve
          earned a reputation for our fanatical obsession with quality.
          Collective experience: Over 70 years Beauty portfolio: Over 40 brands
          Expertise: Local At the heart of the company, is a group of veterans
          with a deep understanding of the industry in general, and the cultural
          nuances of the Indian market in particular. We’ve always believed that
          great quality and great value aren’t mutually exclusive. And no
          product in our portfolio reflects this better than Embark.
        </p>
        <div className="row">
          <div className="col-12 col-lg-6">
            <div className="heading">
              MADE BY SOME OF THE BEST IN THE BUSINESS
            </div>
            <p>Meet the renowned perfumers behind Embark.</p>
          </div>
          <div className="col-12 col-lg-6">
            <p>
              What will you find inside our gorgeous bottles? A product made by
              some of the best perfumers in the business, using outstanding
              essential oils that last longer. One that’s made in India, for
              India. One that’s designed keeping in mind the country’s climate
              as well as your skin. One that’s been tested by both experts and
              consumers. One that is 100% cruelty-free. In short, a product that
              deserves pride of place on your dresser.
            </p>
          </div>
          <div className="col-6 col-lg-3">
            <div className="team">
              <div className="box-image">
                <img
                  width="300"
                  height="300"
                  src="https://embarkperfumes.com/wp-content/uploads/2021/08/hernan_figoli.png"
                  className="attachment- size-"
                  alt="img"
                  decoding="async"
                  loading="lazy"
                  srcset="https://embarkperfumes.com/wp-content/uploads/2021/08/hernan_figoli.png 300w, https://embarkperfumes.com/wp-content/uploads/2021/08/hernan_figoli-280x280.png 280w, https://embarkperfumes.com/wp-content/uploads/2021/08/hernan_figoli-100x100.png 100w"
                  sizes="(max-width: 300px) 100vw, 300px"
                />
              </div>
              <div className="box-text text-center">
                <h5>HERNAN FIGOLI</h5>
              </div>
            </div>
          </div>
          <div className="col-6 col-lg-3">
            <div className="team">
              <div className="box-image">
                <img
                  width="300"
                  height="300"
                  src="https://embarkperfumes.com/wp-content/uploads/2021/08/guillame_flavigny.png"
                  className="attachment- size-"
                  alt="img"
                  decoding="async"
                  loading="lazy"
                  sizes="(max-width: 300px) 100vw, 300px"
                />
              </div>
              <div className="box-text text-center">
                <h5>GUILLAUME FLAVIGNY</h5>
              </div>
            </div>
          </div>
          <div className="col-6 col-lg-3">
            <div className="team">
              <div className="box-image">
                <img
                  width="300"
                  height="300"
                  src="https://embarkperfumes.com/wp-content/uploads/2021/08/prakash_narayan.png"
                  className="attachment- size-"
                  alt="img"
                  decoding="async"
                  loading="lazy"
                  sizes="(max-width: 300px) 100vw, 300px"
                />
              </div>
              <div className="box-text text-center">
                <h5>PRAKASH NARAYA</h5>
              </div>
            </div>
          </div>
          <div className="col-6 col-lg-3">
            <div className="team">
              <div className="box-image">
                <img
                  width="300"
                  height="300"
                  src="	https://embarkperfumes.com/wp-content/uploads/2021/08/nadege_legarlantezec.png"
                  className="attachment- size-"
                  alt="img"
                  decoding="async"
                  loading="lazy"
                  sizes="(max-width: 300px) 100vw, 300px"
                />
              </div>
              <div className="box-text text-center">
                <h5>NADÈGE LE GARLANTE</h5>
              </div>
            </div>
          </div>
        </div>
      </div>
      <MobileNav />
    </>
  );
}

export default AboutUs;